/** utility **/

.hide-scroll-bar::-webkit-scrollbar {
  height: 0px;
  /* Remove scrollbar space */
  background: transparent;
  /* Optional: just make scrollbar invisible */
}

.hide-scroll-bar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
}

.ellipsis--noWrap {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.cursor-pointer {
  cursor: pointer;
}

.white-space-nowrap {
  white-space: nowrap;
}

.Next-Legend {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: fit-content;
}

.Next-Legend-icon {
  border-radius: var(--spacing-s);
  margin-right: var(--spacing);
}

.Next-ProgressBar {
  width: 100%;
  border-radius: var(--spacing-m);
  background: var(--secondary-lighter);
}

.Next-ProgressBar-indicator {
  height: var(--spacing);
  background-color: var(--primary);
  border-radius: var(--spacing-m);
  transition: width 200ms;
}

/* badge */

.Next-Avatar {
  display: flex;
  flex-grow: 0;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  text-transform: uppercase;
  border-radius: 50%;
  cursor: pointer;
}

.Next-Avatar--regular {
  height: var(--spacing-3);
  width: var(--spacing-3);
}

.Next-Avatar--tiny {
  height: var(--spacing-xl);
  width: var(--spacing-xl);
}

.Next-Avatar--disabled {
  cursor: default;
}

.Next-Avatar--primary {
  background: var(--primary);
}

.Next-Avatar--secondary {
  background: var(--secondary-light);
}

.Next-Avatar--success {
  background: var(--success);
}

.Next-Avatar--alert {
  background: var(--alert);
}

.Next-Avatar--warning {
  background: var(--warning);
}

.Next-Avatar--accent1 {
  background: var(--accent1);
}

.Next-Avatar--accent2 {
  background: var(--accent2);
}

.Next-Avatar--accent3 {
  background: var(--accent3);
}

.Next-Avatar--accent4 {
  background: var(--accent4);
}

.Next-Avatar-content--tiny {
  line-height: var(--font-height-s) !important;
  font-size: 10px !important;
}

.Next-Avatar-content--secondary {
  color: var(--inverse) !important;
}

.Next-Avatar-content--success {
  color: var(--success-darker) !important;
}

.Next-Avatar-content--warning {
  color: var(--warning-darker) !important;
}

.Next-Avatar-content--accent1 {
  color: var(--accent1-darker) !important;
}

.Next-Avatar-content--accent4 {
  color: var(--accent4-darker) !important;
}

.Next-AvatarGroup-item {
  margin-right: calc((var(--spacing-s) + var(--spacing-m)) * -1);
}

.Next-AvatarGroup-Popper {
  max-width: var(--spacing-9);
  overflow: hidden;
  box-sizing: border-box;
  word-break: break-word;
  hyphens: auto;
}

.Next-AvatarGroup-TextWrapper {
  padding-right: var(--spacing);
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  overflow-y: auto;
}

.Next-AvatarGroup-TextWrapper::-webkit-scrollbar {
