/* Radio */

.Next-Radio {
  display: flex;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  padding-top: var(--spacing-s);
  padding-bottom: var(--spacing-s);
}

.Next-Radio-outerWrapper {
  position: relative;
  flex-shrink: 0;
  margin-top: var(--spacing-s);
}

.Next-Radio-outerWrapper--regular {
  height: var(--spacing-2);
  width: var(--spacing-2);
}

.Next-Radio-outerWrapper--tiny {
  height: var(--spacing-l);
  width: var(--spacing-l);
}

.Next-Radio-input {
  opacity: 0;
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 2;
  margin: 0;
  cursor: pointer;
}

.Next-Radio-labelWrapper {
  padding-left: var(--spacing);
  display: flex;
  flex-direction: column;
}

.Next-Radio-Label {
  display: flex;
  cursor: pointer;
}

.Next-Radio-wrapper {
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  position: absolute;
  border: var(--border);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  z-index: 1;
}

.Next-Radio--disabled {
  pointer-events: none;
}

.Next-Radio:hover .Next-Radio-wrapper {
  border: var(--spacing-xs) solid var(--inverse-lightest);
}

.Next-Radio:active .Next-Radio-wrapper {
  background-color: var(--secondary-light);
  border: var(--spacing-xs) solid var(--inverse-lightest);
}

.Next-Radio--disabled .Next-Radio-wrapper {
  border: var(--spacing-xs) solid var(--secondary-light);
  background-color: var(--secondary-lightest);
}

.Next-Radio-input:checked ~ .Next-Radio-wrapper {
  border: var(--spacing-xs) solid var(--primary);
}

.Next-Radio-input:checked ~ .Next-Radio-wrapper:focus {
  border: var(--spacing-xs) solid var(--primary-dark);
}

.Next-Radio-input:checked ~ .Next-Radio-wrapper:after {
  display: block;
}

.Next-Radio-wrapper:after {
  content: '';
  display: none;
  border-radius: 50%;
  background: var(--primary);
}

.Next-Radio-wrapper--regular:after {
  width: var(--spacing);
  height: var(--spacing);
}

.Next-Radio-wrapper--tiny:after {
  width: var(--spacing-m);
  height: var(--spacing-m);
  border: var(--spacing-xs) solid var(--primary);
}

.Next-Radio-outerWrapper:hover .Next-Radio-input:checked ~ .Next-Radio-wrapper {
  border: var(--spacing-xs) solid var(--primary-dark);
}

.Next-Radio:hover .Next-Radio-input:checked ~ .Next-Radio-wrapper:after {
  background: var(--primary-dark);
}

.Next-Radio:active .Next-Radio-input:checked ~ .Next-Radio-wrapper {
  background: var(--white);
  border: var(--spacing-xs) solid var(--primary-darker);
}

.Next-Radio-outerWrapper:active .Next-Radio-input:checked ~ .Next-Radio-wrapper:after {
  background: var(--primary-darker);
}

.Next-Radio--disabled .Next-Radio-input:checked ~ .Next-Radio-wrapper {
  border: var(--spacing-xs) solid var(--primary-lighter);
  background-color: var(--white);
}

.Next-Radio--disabled .Next-Radio-input:checked ~ .Next-Radio-wrapper:after {
