.Legend {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: fit-content;
}

.Legend-icon {
  border-radius: var(--spacing-s);
  margin-right: var(--spacing);
}

.ProgressBar {
  width: 100%;
  border-radius: var(--spacing-m);
  background: var(--secondary-lighter);
}

.ProgressBar-indicator {
  height: var(--spacing);
  background-color: var(--primary);
  border-radius: var(--spacing-m);
  transition: width 200ms;
}

/* badge */

.Avatar {
  display: flex;
  flex-grow: 0;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  text-transform: uppercase;
  border-radius: 50%;
  cursor: pointer;
}

.Avatar--regular {
  height: var(--spacing-3);
  width: var(--spacing-3);
}

.Avatar--tiny {
  height: var(--spacing-xl);
  width: var(--spacing-xl);
}

.Avatar--disabled {
  cursor: default;
}

.Avatar--primary {
  background: var(--primary);
}

.Avatar--secondary {
  background: var(--secondary-light);
}

.Avatar--success {
  background: var(--success);
}

.Avatar--alert {
  background: var(--alert);
}

.Avatar--warning {
  background: var(--warning);
}

.Avatar--accent1 {
  background: var(--accent1);
}

.Avatar--accent2 {
  background: var(--accent2);
}

.Avatar--accent3 {
  background: var(--accent3);
}

.Avatar--accent4 {
  background: var(--accent4);
}

.Avatar-content--tiny {
  line-height: var(--font-height-s) !important;
  font-size: 10px !important;
}

.Avatar-content--secondary {
  color: var(--inverse) !important;
}

.Avatar-content--success {
  color: var(--success-darker) !important;
}

.Avatar-content--warning {
  color: var(--warning-darker) !important;
}

.Avatar-content--accent1 {
  color: var(--accent1-darker) !important;
}

.Avatar-content--accent4 {
  color: var(--accent4-darker) !important;
}

.AvatarGroup-item {
  margin-right: calc((var(--spacing-s) + var(--spacing-m)) * -1);
}

.AvatarGroup-Popper {
  max-width: var(--spacing-9);
  overflow: hidden;
  box-sizing: border-box;
  word-break: break-word;
  hyphens: auto;
}

.AvatarGroup-TextWrapper {
  padding-right: var(--spacing);
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  overflow-y: auto;
}

.AvatarGroup-TextWrapper::-webkit-scrollbar {
  width: var(--spacing);
}

.AvatarGroup-TextWrapper::-webkit-scrollbar-thumb {
  background: var(--secondary);
  border-radius: var(--spacing-m);
}

@keyframes backdrop-open {
  from {
    opacity: 0;
  }
  to {
    opacity: 60;
  }
}

@keyframes backdrop-close {
  from {
    opacity: 60;
  }
  to {
    opacity: 0;
  }
}

.Backdrop {
  background-color: color-mod(var(--inverse) a(0.8));
  height: 100vh;
  width: 100vw;
  position: fixed;
  left: 0;
  top: 0;
  display: none;
  align-items: center;
  z-index: 1000;
  overflow: auto;
  flex-direction: column;
  justify-content: center;
  touch-action: none;
  visibility: hidden;
}

.Backdrop--open {
  display: flex;
  visibility: visible;
}

.Backdrop-animation--open {
  animation: backdrop-open 120ms cubic-bezier(0, 0, 0.3, 1);
}

.Backdrop-animation--close {
  animation: backdrop-close 120ms cubic-bezier(0.4, 0.14, 1, 1);
}

/* badge */

.Badge {
  /* 160px */
  max-width: calc(var(--spacing-2) * 10);
  /* truncate */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: var(--font-size-s);
  line-height: var(--font-height-s);
  text-transform: uppercase;
  padding-right: var(--spacing-m);
  padding-left: var(--spacing-m);
  padding-top: var(--spacing-s);
  padding-bottom: var(--spacing-s);
  border-radius: var(--spacing-m);
  display: inline-block;
  box-sizing: border-box;
  font-weight: var(--font-weight-bold);
  cursor: default;
}

.Badge--primary {
  background: var(--primary);
  color: var(--white);
}

.Badge--secondary {
  background: var(--secondary-light);
}

.Badge--success {
  background: var(--success);
  color: var(--white);
}

.Badge--alert {
  background: var(--alert);
  color: var(--white);
}

.Badge--warning {
  background: var(--warning);
  color: var(--warning-darker);
}

.Badge--accent1 {
  background: var(--accent1);
  color: var(--white);
}

.Badge--accent2 {
  background: var(--accent2);
  color: var(--white);
}

.Badge--accent3 {
  background: var(--accent3);
  color: var(--white);
}

.Badge--accent4 {
  background: var(--accent4);
  color: var(--accent4-darker);
}

.Badge--subtle-primary {
  color: var(--primary-dark);
  background: var(--primary-lightest);
}

.Badge--subtle-secondary {
  background: var(--secondary-light);
}

.Badge--subtle-success {
  color: var(--success-dark);
  background: var(--success-lightest);
}

.Badge--subtle-alert {
  color: var(--alert-dark);
  background: var(--alert-lightest);
}

.Badge--subtle-warning {
  color: var(--warning-darker);
  background: var(--warning-lightest);
}

.Badge--subtle-accent1 {
  color: var(--accent1-darker);
  background: var(--accent1-lightest);
}

.Badge--subtle-accent2 {
  color: var(--accent2-dark);
  background: var(--accent2-lightest);
}

.Badge--subtle-accent3 {
  color: var(--accent3-dark);
  background: var(--accent3-lightest);
}

.Badge--subtle-accent4 {
  color: var(--accent4-darker);
  background: var(--accent4-lightest);
}

/* breadcrumbs */

.Breadcrumbs {
  display: flex;
  align-items: center;
}

.Breadcrumbs-item {
  display: flex;
  align-items: center;
}

.Breadcrumbs-link {
  margin: var(--spacing-m);
}

.Breadcrumbs-item:first-child .Breadcrumbs-link {
  margin-left: 0;
}

.Breadcrumbs-itemSeparator {
  color: var(--secondary);
}

.Breadcrumbs-dropdown {
  display: flex;
  align-items: center;
}

.Button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  position: relative;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
  border: 0;
  border-radius: var(--spacing-m);

  padding-top: var(--spacing);
  padding-bottom: var(--spacing);

  text-align: center;
  font-family: var(--font-family);
  font-weight: var(--font-weight-normal);
  font-size: var(--font-size);
  line-height: var(--font-height-s);
  color: var(--text-white);

  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out,
    box-shadow 0.15s ease-in-out;
}

.Button:disabled {
  cursor: not-allowed;
  pointer-events: none;
}

.Button:focus {
  outline: 0;
}

.Button--iconAlign-right {
  flex-direction: row-reverse;
}

.Button--tiny {
  height: var(--spacing-xl);
  padding-right: var(--spacing);
  padding-left: var(--spacing);
  padding-top: var(--spacing-m);
  padding-bottom: var(--spacing-m);
}

.Button--tinySquare {
  width: var(--spacing-xl);
}

.Button--regular {
  height: var(--spacing-3);
  padding-right: var(--spacing-l);
  padding-left: var(--spacing-l);
}

.Button--regularSquare {
  width: var(--spacing-3);
}

.Button--large {
  height: 40px;
  padding-right: var(--spacing-2);
  padding-left: var(--spacing-2);

  line-height: var(--font-height-m);
  font-size: var(--font-size-m);
}

.Button--largeSquare {
  width: 40px;
}

.Button--tinySquare .Button-icon,
.Button--largeSquare .Button-icon,
.Button--tinySquare .Spinner,
.Button--largeSquare .Spinner {
  padding-left: var(--spacing-s);
  padding-right: var(--spacing-s);
}

.Button--expanded {
  width: 100%;
}

.Button-icon {
  line-height: inherit;
  display: flex;
  align-items: center;
}

.Button-icon--left {
  margin-right: var(--spacing);
}

.Button-icon--right {
  margin-left: var(--spacing);
}

.Button--tiny .Button-icon--left {
  margin-right: var(--spacing-m);
}
.Button--tiny .Button-icon--right {
  margin-left: var(--spacing-m);
}

.Button--basic {
  background: var(--secondary-light);
  color: var(--inverse);
}

.Button--basic:hover {
  background: var(--secondary);
}

.Button--basic:active {
  background: var(--secondary-dark);
}

.Button--basic:focus {
  box-shadow: var(--shadow-spread) var(--secondary-shadow);
}

.Button--basic:disabled {
  background: var(--secondary-lighter);
  color: var(--inverse-lightest);
}

.Button--primary {
  background: var(--primary);
}

.Button--primary:hover {
  background: var(--primary-dark);
}

.Button--primary:active {
  background: var(--primary-darker);
}

.Button--primary:focus {
  box-shadow: var(--shadow-spread) var(--primary-shadow);
}

.Button--primary:disabled {
  background: var(--primary-lighter);
}

.Button--success {
  background: var(--primary);
}

.Button--success:hover {
  background: var(--primary-dark);
}

.Button--success:active {
  background: var(--primary-darker);
}

.Button--success:focus {
  box-shadow: var(--shadow-spread) var(--primary-shadow);
}

.Button--success:disabled {
  background: var(--primary-lighter);
}

.Button--alert {
  background: var(--alert);
}

.Button--alert:hover {
  background: var(--alert-dark);
}

.Button--alert:active {
  background: var(--alert-darker);
}

.Button--alert:focus {
  box-shadow: var(--shadow-spread) var(--alert-shadow);
}

.Button--alert:disabled {
  background: var(--alert-lighter);
}

.Button--transparent {
  background: transparent;
  color: var(--inverse);
}

.Button--transparent:hover {
  background: var(--secondary-light);
}

.Button--transparent:focus {
  background: color-mod(var(--stone-light) a(0.2));
  /* box-shadow: var(--shadow-spread) var(--secondary-shadow); */
}

.Button--transparent:active {
  background: var(--secondary);
}

.Button--transparent:focus:active {
  background: var(--secondary);
}

.Button--transparent:disabled {
  background: transparent;
  color: var(--inverse-lightest);
}

.Button-spinner {
  display: flex;
  position: absolute;
  z-index: 1;
}

.Button--tiny .Spinner--small {
  height: var(--font-size-s);
  width: var(--font-size-s);
}

.Button--large .Spinner--small {
  height: var(--font-size-l);
  width: var(--font-size-l);
}

.Button--selected {
  background: var(--primary-lightest);
  color: var(--primary);
}

.Button-text--hidden {
  visibility: hidden;
}

/* calendar */

.Calendar-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.Calendar {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

.Calendar--small {
  padding: var(--spacing-l);
  padding-bottom: var(--spacing);
  width: 212px;
}

.Calendar--date--small {
  height: 248px;
}

.Calendar--month--small {
  height: 192px;
}

.Calendar--year--small {
  height: 192px;
}

.Calendar--large {
  padding-top: var(--spacing-2);
  padding-left: var(--spacing-2);
  padding-right: var(--spacing-2);
  padding-bottom: var(--spacing-l);
  width: 316px;
}

.Calendar--date--large {
  height: 324px;
}

.Calendar--month--large {
  height: 272px;
}

.Calendar--year--large {
  height: 272px;
}

.Calendar-header--large {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: var(--spacing-l);
}

.Calendar-header--small {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: var(--spacing-m);
}

.Calendar-headerIcon {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  user-select: none;
}

.Calendar-headerIcon--prev {
  left: 0;
}

.Calendar-headerIcon--next {
  right: 0;
}

.Calendar-headerContent {
  display: flex;
  white-space: nowrap;
  cursor: pointer;
  padding-top: var(--spacing-m);
  padding-bottom: var(--spacing-m);
  user-select: none;
}

.Calendar-body {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-around;
}

.Calendar-valueRow {
  display: flex;
  justify-content: space-between;
  padding-top: var(--spacing-s);
  padding-bottom: var(--spacing-s);
}

.Calendar-valueWrapper {
  display: inline-flex;
  flex: 1 0 0;
  justify-content: center;
  position: relative;
}

.Calendar-valueWrapper--inRange {
  background: var(--primary-lightest);
}

.Calendar-valueWrapper--inRangeError {
  background: var(--alert-lightest);
}

.Calendar-valueWrapper--start {
  background: linear-gradient(90deg, white 50%, var(--primary-lightest) 50%);
  border-radius: var(--spacing-m) 0 0 var(--spacing-m);
}

.Calendar-valueWrapper--startError {
  background: linear-gradient(90deg, white 50%, var(--alert-lightest) 50%);
}

.Calendar-valueWrapper--end {
  background: linear-gradient(90deg, var(--primary-lightest) 50%, white 50%);
  border-radius: 0 var(--spacing-m) var(--spacing-m) 0;
}

.Calendar-valueWrapper--endError {
  background: linear-gradient(90deg, var(--alert-lightest) 50%, white 50%);
}

.Calendar-valueWrapper--startEnd {
  background: transparent;
}

.Calendar-value {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  cursor: pointer;
  user-select: none;
  border-radius: var(--spacing-m);
}

.Calendar-value:hover {
  background: var(--secondary);
}

.Calendar-value--start:hover,
.Calendar-value--end:hover {
  background: var(--primary-lightest);
}

.Calendar-value--startError:hover,
.Calendar-value--endError:hover {
  background: var(--alert-lightest);
}

.Calendar-value--start,
.Calendar-value--end {
  background: var(--primary-lightest);
}

.Calendar-value--startError,
.Calendar-value--endError {
  background: var(--alert-lightest);
}

.Calendar-value--currDateMonthYear,
.Calendar-value--currDateMonthYear:hover {
  background: var(--primary-lightest) !important;
}

.Calendar-value--active,
.Calendar-value--active:hover {
  background: var(--primary) !important;
  font-weight: var(--font-weight-bold);
}

.Calendar-yearValue,
.Calendar-monthValue {
}

.Calendar-yearValue--small,
.Calendar-monthValue--small {
  height: var(--spacing-xl);
  width: var(--spacing-4);
}

.Calendar-yearValue--large,
.Calendar-monthValue--large {
  height: var(--spacing-3);
  width: var(--spacing-5);
}

.Calendar-dateValue {
}

.Calendar-dateValue--small {
  height: var(--spacing-xl);
  width: var(--spacing-xl);
}

.Calendar-dateValue--large {
  height: var(--spacing-3);
  width: var(--spacing-3);
}

.Calendar-valueWrapper--dummy {
  opacity: var(--transparent-58);
}

.Calendar-value--disabled {
  pointer-events: none;
}

.Calendar-dayValues {
  display: flex;
  justify-content: center;
  padding-top: var(--spacing);
  padding-bottom: var(--spacing);
}

.Calendar-dateValues {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-around;
}

.Calendar-dayValues .Calendar-value {
  padding: 0;
}

.Calendar-eventsIndicator {
  position: absolute;
  bottom: var(--spacing-s);
  width: var(--spacing-m);
  height: var(--spacing-m);
  background-color: var(--alert);
  border-radius: 50%;
}
.Calendar-eventsIndicator--small {
  width: 3px;
  height: 3px;
}
.Calendar-eventsIndicator--active {
  background-color: var(--white);
}

/* badge */

.Card {
  border-radius: var(--spacing-m);
  border: var(--spacing-xs) solid var(--secondary-light);
  position: relative;
  overflow: hidden;
  background-color: var(--white);
}

.Card--shadow-default,
.Card--shadow-light,
.Card--shadow-medium,
.Card--shadow-dark {
  border: none;
  box-shadow: var(--shadow-s);
}

.Card-header {
  padding: var(--spacing-2) var(--spacing-2) var(--spacing-l) var(--spacing-2);
}

.Card-body {
  padding-left: var(--spacing-2);
  padding-right: var(--spacing-2);
}

.Card-footer {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: var(--spacing-2);
}

.Card-footer--withSeperator {
  border-top: var(--spacing-xs) solid var(--secondary-light);
}

.CardSubdued {
  background: var(--card-subdued-bg);
  padding: var(--spacing-2);
}

.CardSubdued--top {
  border-top: var(--spacing-xs) solid var(--secondary-light);
}

.CardSubdued--left {
  border-left: var(--spacing-xs) solid var(--secondary-light);
}

.CardSubdued--right {
  border-right: var(--spacing-xs) solid var(--secondary-light);
}

.CardSubdued--bottom {
  border-bottom: var(--spacing-xs) solid var(--secondary-light);
}

.Box {
  width: 100%;
  border-radius: var(--spacing);
  padding: var(--spacing-l);
}

.Box--typing {
  padding-top: var(--spacing);
  padding-bottom: var(--spacing);
}

.Box--incoming {
  border: var(--spacing-xs) solid var(--secondary-light);
  background-color: var(--white);
}

.Box--outgoing {
  background-color: var(--secondary-lightest);
}

.Box-incoming--withStatus {
  border-bottom-left-radius: 0px;
}

.Box-outgoing--withStatus {
  border-bottom-right-radius: 0px;
}

.Box--urgent {
  border-color: var(--alert);
}

.ChatMessage-status {
  margin-top: var(--spacing-m);
  display: block;
}

/* badge */

.Checkbox {
  display: flex;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  line-height: initial;
  box-sizing: border-box;
  padding-top: var(--spacing-s);
  padding-bottom: var(--spacing-s);
}

.Checkbox-input {
  position: absolute;
  z-index: 2;
  opacity: 0;
  cursor: pointer;
  height: 100%;
  width: 100%;
  margin: 0;
}

.Checkbox-labelWrapper {
  padding-left: var(--spacing);
  display: flex;
  flex-direction: column;
  min-width: 0;
}

.Checkbox-label {
  display: flex;
  cursor: pointer;
}

.Checkbox-outerWrapper {
  position: relative;
  margin-top: var(--spacing-s);
}

.Checkbox-outerWrapper--regular {
  height: var(--spacing-2);
  min-width: var(--spacing-2);
}

.Checkbox-outerWrapper--tiny {
  height: var(--spacing-l);
  min-width: var(--spacing-l);
}

/* Type: Unchecked  */

.Checkbox-wrapper {
  top: 0;
  left: 0;
  border: var(--border);
  border-radius: var(--spacing-s);
  display: flex;
  align-items: center;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
}

.Checkbox-input:focus {
  outline: 0;
}

.Checkbox-input:focus ~ .Checkbox-wrapper {
  box-shadow: var(--shadow-spread) var(--secondary-shadow);
}

.Checkbox-input:hover ~ .Checkbox-wrapper {
  border: var(--spacing-xs) solid var(--inverse-lightest);
  background-color: var(--shadow-0);
}

.Checkbox-input:active ~ .Checkbox-wrapper {
  border: var(--spacing-xs) solid var(--inverse-lightest);
  background-color: var(--secondary-light);
}

.Checkbox--disabled {
  pointer-events: none;
}

.Checkbox--disabled .Checkbox-wrapper {
  border: var(--spacing-xs) solid var(--secondary-light);
  background-color: var(--secondary-lightest);
}

/* Type: checked && Type: indeterminate  */

.Checkbox-input--checked ~ .Checkbox-wrapper,
.Checkbox-input--indeterminate ~ .Checkbox-wrapper {
  background-color: var(--primary);
  border: 0;
}

.Checkbox-input--checked:focus ~ .Checkbox-wrapper,
.Checkbox-input--indeterminate:focus ~ .Checkbox-wrapper {
  box-shadow: var(--shadow-spread) var(--primary-shadow);
  background-color: var(--primary);
  border: 0;
}

.Checkbox-input--checked:hover ~ .Checkbox-wrapper,
.Checkbox-input--indeterminate:hover ~ .Checkbox-wrapper {
  background-color: var(--primary-dark);
  border: 0;
}

.Checkbox-input--checked:active ~ .Checkbox-wrapper,
.Checkbox-input--indeterminate:active ~ .Checkbox-wrapper {
  background-color: var(--primary-darker);
  border: 0;
}

.Checkbox--disabled .Checkbox-input--checked ~ .Checkbox-wrapper,
.Checkbox--disabled .Checkbox-input--indeterminate ~ .Checkbox-wrapper {
  background-color: var(--primary-lighter);
  border: 0;
}

.Chip {
  display: flex;
  box-sizing: border-box;
  border-radius: var(--spacing-l);
  padding-right: var(--spacing);
  padding-left: var(--spacing);
  padding-top: var(--spacing-s);
  padding-bottom: var(--spacing-s);
  width: fit-content;
  justify-content: space-between;
  border-style: solid;
  border-width: var(--spacing-xs);
  align-items: center;
  flex-direction: row;
  cursor: default;
  height: var(--spacing-xl);
}
.Chip-wrapper {
  display: inline-flex;
  align-items: center;
}

.Chip-icon--left {
  padding-right: var(--spacing-m);
  display: flex;
  align-items: center;
}

.Chip-icon--right {
  padding-left: var(--spacing-m);
  display: flex;
  align-items: center;
}

.Chip--action {
  background: var(--shadow-0);
  border-color: var(--stone);
  cursor: pointer;
}

.Chip--action:hover {
  background: var(--stone-light);
  border-color: var(--stone);
