}

.Navigation-menu--horizontal {
  height: var(--spacing-3);
  padding-right: var(--spacing-l);
  padding-left: var(--spacing-l);
  border-radius: var(--spacing-2);
  margin: 0 var(--spacing-xs);
}

.Navigation-menu--disabled {
  pointer-events: none;
}

.Navigation-menu:hover {
  background-color: var(--secondary-light);
}

.Navigation-menu--active,
.Navigation-menu--active:hover {
  background: var(--primary-lightest);
}

.Navigation-menu--vertical {
  /* padding-top: var(--spacing-m); */
  /* padding-bottom: var(--spacing-m); */
  padding-left: var(--spacing);
  margin-top: var(--spacing-m);
  margin-bottom: var(--spacing-m);
  /* margin-right: var(--spacing); */
  border-radius: 0px var(--spacing-2) var(--spacing-2) 0px;
}

.Navigation-menu--rounded {
  border-radius: var(--spacing-2);
}

.Navigation-menu--subMenu {
  padding-top: var(--spacing-s);
  padding-bottom: var(--spacing-s);
  padding-left: 40px;
  margin-top: 0;
}

.Navigation-menuIcon {
  padding: 8px;
  border-radius: 50%;
  cursor: pointer;
}

.Navigation-menuIcon--active {
  background: var(--primary-lightest);
}

.Navigation-menuLabel {
  display: flex;
  flex-grow: 1;
  overflow: hidden;
}

.Navigation-menu .Text {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.Navigation-horizontalPills {
  margin-right: var(--spacing-m);
}

.Navigation-horizontalPills--disabled {
  opacity: 0.6;
}

.Navigation-footer {
  display: flex;
  align-items: center;
  padding: var(--spacing-m) var(--spacing);
}

.Navigation-footer--border {
  border-top: var(--border);
}

.Navigation--collapsed .Navigation-menuIcon--footer {
  transform: rotate(180deg);
}

.OverlayHeader {
  box-sizing: border-box;
  margin-left: var(--spacing-xl);
}

.OverlayHeader--withBackButton {
  margin-left: var(--spacing-2);
}

.OverlayHeader-headerWrapper {
  display: flex;
  align-items: center;
}

.OverlayHeader-subheading {
  margin-top: var(--spacing-s);
}

.OverlayHeader-subheading--withBackButton {
  margin-left: var(--spacing);
}

.OverlayBody {
  overflow-y: auto;
}

.Overlay-body--stickFooter {
  margin-bottom: 80px;
}

.OverlayFooter {
  display: flex;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
  padding-left: var(--spacing-xl);
  padding-right: var(--spacing-xl);
}

.OverlayBody::-webkit-scrollbar {
  width: var(--spacing-2);
}

.OverlayBody::-webkit-scrollbar-thumb {
  border: var(--spacing-m) solid rgba(0, 0, 0, 0);
  background-clip: padding-box;
  border-radius: var(--spacing);
  background-color: var(--secondary);
}

.PageHeader-wrapper {
  padding: var(--spacing) var(--spacing-2);
}
.PageHeader-wrapper--separator {
  border-bottom: var(--border);
}

.PageHeader-wrapper--withTabs {
  padding-bottom: 0;
}

.PageHeader-wrapper--withTabs .TabsWrapper-header {
  border-bottom: none;
}

.PageHeader .Row {
  width: 100%;
}

.PageHeader-titleWrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.PageHeader-titleWrapper .Badge {
  margin-left: var(--spacing-l);
}

.PageHeader-statusWrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.PageHeader-statusWrapper .StatusHints {
  margin-right: var(--spacing);
}

.PageHeader-navigationWrapper {
  margin-top: var(--spacing-l);
}

.PageHeader .PageHeader-navigationWrapper {
  justify-content: center;
  align-items: center;
  margin-top: 0 !important;
}

.PageHeader-title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* pagination */

.Pagination {
  display: flex;
  vertical-align: middle;
  flex-direction: row;
  flex-wrap: wrap;
}

.Pagination .Button {
  padding-left: var(--spacing);
  padding-right: var(--spacing);
}

.Pagination-buttonWrapper {
  display: flex;
  flex-direction: row;
}

.Pagination-pageIndex {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: var(--spacing-l);
  padding-right: var(--spacing-l);
}

.Pagination-MetricInput {
  width: var(--spacing-4);
  margin-right: var(--spacing);
  padding-left: var(--spacing-m);
  padding-right: var(--spacing-m);
}

.Pagination-MetricInput .MetricInput-input {
  text-align: center;
}
.Pagination-MetricInput .MetricInput-arrowIcons {
  display: none;
}

@media (max-width: 575px) {
  .Pagination-pageIndex {
    order: -1;
    flex-basis: 100%;
  }
  .Pagination-buttonWrapper {
    margin-top: var(--spacing);
    width: 50%;
  }
  .Pagination-buttonWrapper--next {
    justify-content: flex-start;
  }
  .Pagination-buttonWrapper--previous {
    justify-content: flex-end;
  }
}

.Pills {
  /* 160px */
  max-width: calc(var(--spacing-2) * 10);
  /* truncate */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: var(--font-size-s);
  line-height: var(--font-height-s);
  text-transform: uppercase;
  padding-right: 6px;
  padding-left: 6px;
  padding-top: var(--spacing-s);
  padding-bottom: var(--spacing-s);
  border-radius: 20px;
  display: inline-block;
  box-sizing: border-box;
  font-weight: var(--font-weight-bold);
  cursor: default;
}

@keyframes shimmer {
  0% {
    background-position: -500px 0;
  }
  100% {
    background-position: 500px 0;
  }
}

.Placeholder--animation {
  border-radius: var(--spacing-m);
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: shimmer;
  animation-timing-function: linear;
  background: var(--secondary-lightest);
  background: linear-gradient(
    to right,
    var(--secondary-lighter) 8%,
    var(--secondary-dark) 18%,
    var(--secondary-lighter) 33%
  );
  background-size: 1000px 104px;
}

.Placeholder {
  display: flex;
  flex-grow: 1;
  flex-shrink: 0;
  align-items: center;
}

.Placeholder--withImage {
  display: flex;
  justify-content: flex-start;
}

.Placeholder-paragraph {
  display: flex;
  flex-direction: column;
  flex: 1 0;
}

.Placeholder-paragraph--withImage {
  margin-left: var(--spacing);
}

.PlaceholderParagraph {
  display: flex;
  flex-grow: 1;
  height: var(--font-size);
}

.PlaceholderParagraph-wrapper {
  display: flex;
  align-items: center;
  height: var(--font-height);
}

.PlaceholderParagraph-wrapper--length-small {
  width: 33%;
}

.PlaceholderParagraph-wrapper--length-medium {
  width: 66%;
}

.PlaceholderParagraph-wrapper--length-large {
  width: 100%;
}

.PlaceholderParagraph-wrapper--size-xxs {
  height: var(--font-height-s);
}

.PlaceholderParagraph-wrapper--size-xs {
  height: var(--font-height-m);
}

.PlaceholderParagraph-wrapper--size-s {
  height: var(--font-height-s);
}

.PlaceholderParagraph-wrapper--size-m {
  height: var(--font-height-m);
}

.PlaceholderParagraph-wrapper--size-l {
  height: var(--font-height-l);
}

.PlaceholderParagraph-wrapper--size-xl {
  height: var(--font-height-xl);
}

.PlaceholderParagraph-wrapper--size-xxl {
  height: var(--font-height-xxl);
}

.PlaceholderParagraph-wrapper--size-xxxl {
  height: var(--font-height-xxl);
}

.PlaceholderParagraph--xxs {
  height: var(--font-size-s);
}

.PlaceholderParagraph--xs {
  height: var(--font-size-s);
}

.PlaceholderParagraph--s {
  height: var(--font-size);
}

.PlaceholderParagraph--m {
  height: var(--font-size-m);
}

.PlaceholderParagraph--l {
  height: var(--font-size-l);
}

.PlaceholderParagraph--xl {
  height: var(--font-size-xl);
}

.PlaceholderParagraph--xxl {
  height: var(--font-size-xxl);
}

.PlaceholderParagraph--xxxl {
  height: var(--font-size-xxxl);
}

.PlaceholderImage {
  display: flex;
  flex-shrink: 0;
  width: 16px;
  height: 16px;
}

.PlaceholderImage--small {
  width: 16px;
  height: 16px;
}

.PlaceholderImage--medium {
  width: 32px;
  height: 32px;
}

.PlaceholderImage--large {
  width: 48px;
  height: 48px;
}

.PlaceholderImage--round {
  border-radius: 50%;
}

.Popover {
  border-radius: var(--spacing-m);
  z-index: 500;
  background: var(--white);
  box-shadow: var(--shadow-l);
}

.Popover--dark {
  background: var(--inverse);
  color: var(--white);
}

.Popover[data-hide='true'] {
  visibility: hidden;
  pointer-events: none;
}

@keyframes popper-fade-in {
  0% {
    opacity: 0;
  }

  80% {
    opacity: 0.2;
  }

  100% {
    opacity: 1;
  }
}

.Ring {
  transform: rotate(-90deg);
}

.Ring--regular {
  height: var(--spacing-3);
  width: var(--spacing-3);
}

.Ring--small {
  height: var(--spacing-2);
  width: var(--spacing-2);
}

.Ring Circle {
  fill: none;
}

.Ring-background {
  stroke: var(--secondary-lightest);
}

.Ring-indicator {
  stroke: var(--primary);
  transition: stroke-dashOffset 200ms;
}

/* Radio */

.Radio {
  display: flex;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  padding-top: var(--spacing-s);
  padding-bottom: var(--spacing-s);
}

.Radio-outerWrapper {
  position: relative;
  flex-shrink: 0;
  margin-top: var(--spacing-s);
}

.Radio-outerWrapper--regular {
  height: var(--spacing-2);
  width: var(--spacing-2);
}

.Radio-outerWrapper--tiny {
  height: var(--spacing-l);
  width: var(--spacing-l);
}

.Radio-input {
  opacity: 0;
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 2;
  margin: 0;
  cursor: pointer;
}

.Radio-labelWrapper {
  padding-left: var(--spacing);
  display: flex;
  flex-direction: column;
}

.Radio-Label {
  display: flex;
  cursor: pointer;
}

.Radio-wrapper {
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  position: absolute;
  border: var(--border);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  z-index: 1;
}

.Radio--disabled {
  pointer-events: none;
}

.Radio:hover .Radio-wrapper {
  border: var(--spacing-xs) solid var(--inverse-lightest);
}

.Radio:active .Radio-wrapper {
  background-color: var(--secondary-light);
  border: var(--spacing-xs) solid var(--inverse-lightest);
}

.Radio--disabled .Radio-wrapper {
  border: var(--spacing-xs) solid var(--secondary-light);
  background-color: var(--secondary-lightest);
}

.Radio-input:checked ~ .Radio-wrapper {
  border: var(--spacing-xs) solid var(--primary);
}

.Radio-input:checked ~ .Radio-wrapper:focus {
  border: var(--spacing-xs) solid var(--primary-dark);
}

.Radio-input:checked ~ .Radio-wrapper:after {
  display: block;
}

.Radio-wrapper:after {
  content: '';
  display: none;
  border-radius: 50%;
  background: var(--primary);
}

.Radio-wrapper--regular:after {
  width: var(--spacing);
  height: var(--spacing);
}

.Radio-wrapper--tiny:after {
  width: var(--spacing-m);
  height: var(--spacing-m);
  border: var(--spacing-xs) solid var(--primary);
}

.Radio-outerWrapper:hover .Radio-input:checked ~ .Radio-wrapper {
  border: var(--spacing-xs) solid var(--primary-dark);
}

.Radio:hover .Radio-input:checked ~ .Radio-wrapper:after {
  background: var(--primary-dark);
}

.Radio:active .Radio-input:checked ~ .Radio-wrapper {
  background: var(--white);
  border: var(--spacing-xs) solid var(--primary-darker);
}

.Radio-outerWrapper:active .Radio-input:checked ~ .Radio-wrapper:after {
  background: var(--primary-darker);
}

.Radio--disabled .Radio-input:checked ~ .Radio-wrapper {
  border: var(--spacing-xs) solid var(--primary-lighter);
  background-color: var(--white);
}

.Radio--disabled .Radio-input:checked ~ .Radio-wrapper:after {
  background: var(--primary-lighter);
}

@keyframes sidesheet-open {
  from {
    right: -100%;
  }
  to {
    right: 0;
  }
}

@keyframes sidesheet-close {
  from {
    right: 0;
  }
  to {
    right: -100%;
  }
}

@keyframes shift-right-spacing-3 {
  from {
    transform: translateX(calc(-1 * var(--spacing-3)));
  }
  to {
    transform: translateX(0);
  }
}

@keyframes shift-left-spacing-3 {
  from {
    transform: translateX(var(--spacing-3));
  }
  to {
    transform: translateX(0);
  }
}

.Sidesheet {
  position: fixed !important;
  top: 0;
  right: 0;
  height: 100vh;
  box-shadow: var(--shadow-l);
  background-color: var(--white);
  display: flex;
  flex-direction: column;
  visibility: hidden;
}

.Sidesheet--open {
  visibility: visible;
}

.Sidesheet-animation--open {
  animation: sidesheet-open 240ms cubic-bezier(0, 0, 0.3, 1);
}

.Sidesheet-animation--close {
  animation: sidesheet-close 120ms cubic-bezier(0.4, 0.14, 1, 1);
  animation-fill-mode: forwards;
}

.Sidesheet-header {
  display: flex;
  justify-content: flex-start;
  padding-top: var(--spacing-2);
  padding-bottom: var(--spacing-l);
  padding-right: var(--spacing-2);
}

.Sidesheet-header--withSeperator {
  border-bottom: var(--border);
}

.Sidesheet-body {
  padding: 0 var(--spacing-xl);
}

.Sidesheet-body--withMargin {
  margin-bottom: 80px;
}

.Sidesheet-footer {
  height: 80px;
}

.Sidesheet-footer--withSeperator {
  border-top: var(--border);
}

.Sidesheet-footer--stickToBottom {
  position: absolute;
  bottom: 0;
}

.Sidesheet-header--shiftRight {
  animation: shift-right-spacing-3 80ms cubic-bezier(0.2, 0, 0.38, 0.9);
}

.Sidesheet-header--shiftLeft {
  animation: shift-left-spacing-3 80ms cubic-bezier(0.2, 0, 0.38, 0.9);
}

.Slider {
  width: 100%;
}

.Slider-wrapper {
  position: relative;
  outline: none;
  cursor: pointer;
}

.Slider-wrapper--disabled {
  pointer-events: none;
}

.Slider-track {
  border-radius: var(--spacing-m);
  height: var(--spacing-2);
  display: flex;
  align-items: center;
  overflow: hidden;
}

.Slider-progress {
  background: var(--secondary-lighter);
  height: var(--spacing-s);
  box-sizing: border-box;
}

.Slider-progress--inRange {
  background: var(--primary);
}

.Slider-progress--inRangeDisabled {
  background: var(--secondary-light);
  border: var(--border);
}

.Slider-label {
  margin-top: var(--spacing-m);
  -webkit-transform: translate(-50%, 0px);
  transform: translate(-50%, 0px);
  display: flex;
  align-items: center;
  flex-direction: column;
  position: absolute;
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.Slider-ticks {
  width: var(--spacing-s);
  height: var(--spacing-m);
  border-radius: var(--spacing-xs);
  background-color: var(--secondary-dark);
}

.Slider-tooltip--visible {
  visibility: visible;
}

.Slider-tooltip--hidden {
  visibility: hidden;
}

.Slider-tooltip {
  position: absolute;
  color: var(--white);
  outline: none;
  bottom: calc(100% + 4px);
  transform: translateX(calc(-50% + 8px));
}

.Slider-handle {
  height: var(--spacing-2);
  width: var(--spacing-2);
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 50%;
  background-color: var(--white);
  box-shadow: var(--shadow-s);
  cursor: pointer;
  box-sizing: border-box;
  outline: none;
  display: flex;
}

.Slider-handle:hover {
  background-color: var(--secondary-lightest);
  border: var(--border);
}

.Slider-handle:focus,
.Slider-handle:active {
  border: var(--spacing-s) solid var(--primary);
}

.Slider-handle--disabled {
  pointer-events: none;
  background-color: var(--secondary-light);
  box-shadow: none;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px;
  }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px;
  }
}

.Spinner {
  animation: rotate 2s linear infinite;
}

.Spinner--small {
  height: var(--spacing-2);
  width: var(--spacing-2);
}

.Spinner--medium {
  height: var(--spacing-3);
  width: var(--spacing-3);
}

.Spinner--large {
  height: var(--spacing-4);
  width: var(--spacing-4);
}

.Circle {
  stroke-linecap: round;
  stroke-dasharray: 1 200;
  stroke-dashoffset: 0;
  animation: spin 1.5s ease-in-out infinite;
}

.Circle--primary {
  stroke: var(--primary);
}

.Circle--secondary {
  stroke: var(--secondary-dark);
}

.Circle--white {
  stroke: var(--white);
}

.StatusHint {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: fit-content;
}

.StatusHint-icon {
  flex-shrink: 0;
  border-radius: 50%;
  height: var(--spacing);
  width: var(--spacing);
  margin-right: var(--spacing);
}

.StatusHint--alert {
  background: var(--alert);
}

.StatusHint--success {
  background: var(--success);
}

.StatusHint--warning {
  background: var(--warning);
}

.StatusHint--info {
  background: var(--primary);
}

.StatusHint--default {
  background: var(--secondary);
}

.Stepper {
  display: flex;
}

.Step {
  display: flex;
  align-items: center;
  padding-left: var(--spacing-l);
  padding-right: var(--spacing-l);
  margin-left: var(--spacing-xs);
  margin-right: var(--spacing-xs);
  cursor: pointer;
}

.Step--active {
  background-color: var(--primary-lightest);
  border-radius: var(--spacing-2);
  pointer-events: none;
}

.Step--disabled {
  cursor: not-allowed;
}

.Switch {
  display: inline-flex;
  position: relative;
}

.Switch--tiny {
  height: var(--spacing-2);
  width: var(--spacing-3);
}

.Switch--regular {
  height: var(--spacing-xl);
  width: var(--spacing-4);
}

.Switch--large {
  height: var(--spacing-3);
  width: var(--spacing-5);
}

.Switch-input {
  position: absolute;
  cursor: pointer;
  z-index: 10;
  opacity: 0;
  width: 100%;
  height: 100%;
  margin: 0;
}

.Switch-wrapper {
  display: flex;
  align-items: center;
  line-height: 100%;
  padding: var(--spacing-s);
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--secondary-light);
  transition: background 120ms ease;
  padding-right: 6px;
}

.Switch-input:focus ~ .Switch-wrapper {
  box-shadow: var(--shadow-spread) var(--secondary-shadow);
}

.Switch-input:focus ~ .Switch-wrapper--checked {
  box-shadow: var(--shadow-spread) var(--primary-shadow);
}

.Switch-input:hover ~ .Switch-wrapper {
  background-color: var(--secondary-light);
  box-shadow: var(--shadow-s);
}

.Switch-input:hover ~ .Switch-wrapper--checked {
  background-color: var(--primary-dark);
  box-shadow: var(--shadow-s);
}

.Switch-input:active ~ .Switch-wrapper {
  background-color: var(--secondary-dark);
}

.Switch-input:active ~ .Switch-wrapper--checked {
  background-color: var(--primary-darker);
}

.Switch-wrapper--tiny {
  border-radius: 10px;
}

.Switch-wrapper--regular {
  border-radius: 34px;
}

.Switch-wrapper--large {
  border-radius: 38px;
}

.Switch-wrapper:before {
  content: '';
  height: 100%;
  width: 50%;
  background-color: var(--shadow-0);
  box-shadow: 0 0 var(--spacing-xs) 0 var(--shadow-20);
  border-radius: 50%;
  box-sizing: border-box;
  transition-duration: 80ms;
}

.Switch-wrapper--checked {
  background-color: var(--primary);
  transition: background 120ms ease;
  padding-left: 6px;
  padding-right: 2px !important;
}

.Switch-wrapper--checked:before {
  -webkit-transform: translateX(100%);
  -ms-transform: translateX(100%);
  transform: translateX(100%);
  transition-duration: 80ms;
}

.Switch--disabled {
  pointer-events: none;
}

.Switch-wrapper--disabled:before {
  background-color: var(--secondary-lightest);
}

.Switch-wrapper--checkedDisabled {
  background-color: var(--primary-lighter);
}

.Table {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  height: 100%;
  width: 100%;
  z-index: 1;
}

.Table-grid {
  height: 100%;
  overflow: hidden;
  z-index: 1;
}

.Table-pagination {
  display: flex;
  justify-content: center;
  background: var(--white);
  padding: var(--spacing) 0;
  border-top: var(--border);
}

/* tabs */

.TabsWrapper {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
}

.TabsWrapper-header {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  flex-direction: row;
}

.TabsWrapper-header--withSeparator {
  border-bottom: var(--border);
}

.Tab {
  position: relative;
  display: flex;
  flex-direction: row;
  cursor: pointer;
  min-width: 40px;
  padding: var(--spacing-l);
  margin-left: calc(-1 * var(--spacing-l));
  margin-right: var(--spacing-l);
  margin-bottom: calc(-1 * var(--spacing-xs));
  text-align: center;
}

.Tab:last-child {
  margin-right: 0;
}

.Tab::after {
  position: absolute;
  left: 0;
  bottom: 0;
  content: '';
  width: calc(100% - 2 * var(--spacing-l));
  height: var(--spacing-s);
  background-color: transparent;
  margin-left: var(--spacing-l);
  border-top-left-radius: var(--spacing-xs);
  border-top-right-radius: var(--spacing-xs);
}

.Tab:hover::after {
  background-color: var(--primary-dark);
}

.Tab:active::after {
  background-color: var(--primary-darker);
}

.Tab--active::after {
  background-color: var(--primary);
}

.Tab:focus {
  outline: none;
  border-radius: 4px;
  /* Using box shadow instead of border as border shifts the div down which cuts the Icon & Text of label. */
  box-shadow: color-mod(var(--primary) a(0.2)) 0px 0px 0px var(--spacing-s) inset;
}

.Tab:active:focus {
  box-shadow: none;
}

.Tab--disabled {
  cursor: not-allowed;
}

.Tab--disabled:hover::after {
  background-color: transparent;
}

.Tab-count {
  display: flex;
  margin-right: var(--spacing-l);
}

.Tab-count .Badge {
  margin-left: 0;
  margin-right: 0;
}

.Tab-icon {
  display: flex;
  min-width: inherit;
  justify-content: space-between;
}

.Tabs {
  display: flex;
  box-sizing: border-box;
  flex-direction: row;
}

.Tabs--withSeparator {
  border-bottom: var(--border);
}

.Tab-pills {
  cursor: pointer;
  margin-right: var(--spacing);
}

.Tab-pills--disabled {
  opacity: 60%;
}

/* Textarea */

.Textarea {
  min-height: var(--spacing-3);
  width: 100%;
  display: block;
  font-family: var(--font-family);
  font-weight: var(--font-weight-normal);
  line-height: var(--font-height);
  font-size: var(--font-size);
  box-sizing: border-box;
  border-radius: var(--spacing-m);
  border: var(--border);
  color: var(--inverse);
  padding: var(--spacing) var(--spacing-l);
  resize: none;
}

.Textarea::placeholder {
  color: var(--inverse-lighter);
}

.Textarea::-ms-clear {
  display: none;
}

.Textarea:focus-within {
  outline: none;
  border: var(--spacing-xs) solid var(--primary);
}

.Textarea:disabled {
  color: var(--inverse-lighter);
  background: var(--secondary-lightest);
  border-color: var(--secondary-light);
}

.Textarea--error {
  border: var(--spacing-xs) solid var(--alert);
}

.Textarea--error:focus-within {
  border: var(--spacing-xs) solid var(--alert);
  box-shadow: var(--shadow-spread) var(--alert-shadow);
}

.Textarea--resize {
  resize: auto;
}

.Textarea::-webkit-scrollbar {
  width: var(--spacing-2);
}

.Textarea::-webkit-scrollbar-thumb {
  border: var(--spacing-m) solid rgba(0, 0, 0, 0);
  background-clip: padding-box;
  border-radius: var(--spacing);
  background-color: var(--secondary);
}

/* toast */

.Toast {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;

  width: 360px;
  border-radius: var(--spacing-m);
  padding-left: var(--spacing-2);
  padding-right: var(--spacing-2);
  padding-top: var(--spacing-l);
  padding-bottom: var(--spacing-l);
}

.Toast--withMessage {
  padding-bottom: var(--spacing-2);
}

.Toast--info {
  background-color: var(--primary);
}

.Toast--alert {
  background-color: var(--alert);
}

.Toast--success {
  background-color: var(--success);
}

.Toast--warning {
  background-color: var(--warning);
}

.Toast--warning {
  color: var(--text);
}

.Toast-icon {
  display: flex;
  line-height: var(--font-height-m);
}

.Toast-icon--left {
  margin-right: var(--spacing-2);
}

.Toast-icon--right {
  cursor: pointer;
  margin-left: var(--spacing-2);
}

.Toast-icon--info,
.Toast-icon--success,
.Toast-icon--alert,
.Toast-icon--default {
  color: var(--text-white);
}

.Toast-icon--warning {
  color: var(--warning-darker);
}

.Toast-body {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.Toast-title {
  display: flex;
}

.Toast-heading {
  flex-grow: 1;
}

.Toast-text {
  padding-top: var(--spacing);
}

.Toast-text--warning,
.Toast-heading--warning {
  color: var(--warning-darker) !important;
}

.Toast-actions {
  display: flex;
  padding-top: var(--spacing-l);
}

.Toast-actionButton {
  color: var(--text-white);
  border-radius: var(--spacing-m);
  margin-right: var(--spacing);
}

.Toast-actionButton:last-child {
  margin-right: 0;
}

.Toast-actionButton--default {
  background: var(--inverse-light);
}

.Toast-actionButton--default:hover,
.Toast-actionButton--default:active {
  background: var(--inverse);
}

.Toast-actionButton--default:focus {
  box-shadow: var(--shadow-spread) color-mod(var(--inverse-lighter) a(0.16));
}

.Toast-actionButton--info {
  background: var(--primary-dark);
}

.Toast-actionButton--info:hover,
.Toast-actionButton--info:active {
  background: var(--primary-darker);
}

.Toast-actionButton--info:focus {
  box-shadow: var(--shadow-spread) color-mod(var(--primary-dark) a(0.16));
}

.Toast-actionButton--success {
  background: var(--success-dark);
}

.Toast-actionButton--success:hover,
.Toast-actionButton--success:active {
  background: var(--success-darker);
}

.Toast-actionButton--success:focus {
  box-shadow: var(--shadow-spread) color-mod(var(--success-dark) a(0.16));
}

.Toast-actionButton--alert {
  background: var(--alert-dark);
}

.Toast-actionButton--alert:hover,
.Toast-actionButton--alert:active {
  background: var(--alert-darker);
}

.Toast-actionButton--alert:focus {
  box-shadow: var(--shadow-spread) var(--alert-shadow);
}

.Toast-actionButton--warning {
  background: var(--warning-dark);
}

.Toast-actionButton--warning:hover,
.Toast-actionButton--warning:active {
  background: var(--warning-darker);
}

.Toast-actionButton--warning:focus {
  box-shadow: var(--shadow-spread) var(--warning-shadow);
}

.Tooltip {
  max-width: var(--spacing-9);
  padding: var(--spacing) var(--spacing-2);
  border-radius: var(--spacing-m);
  z-index: 500;
  background: var(--inverse);
  overflow: hidden;
  box-sizing: border-box;
  transition: opacity 120ms;
  transition-delay: 800ms;
  transition-timing-function: cubic-bezier(0, 0, 0.38, 0.9);
}

.Tooltip-text {
  word-break: break-word;
  hyphens: auto;
}

body {
  font-family: var(--font-family);
  color: var(--text);
  line-height: var(--font-height);
  margin: 0;
  padding: 0;
  font-size: var(--font-size);
}

/* Text */
.Text {
  margin: 0;
  line-height: var(--font-height);
}

.Text--small {
  font-size: var(--font-size-s);
  line-height: var(--font-height-normal);
}

.Text--regular {
  font-size: var(--font-size);
  line-height: var(--font-height);
}

.Text--large {
  font-size: var(--font-size-m);
  line-height: var(--font-height-m);
}

.Text--default {
  color: var(--text);
}

.Text--subtle {
  color: var(--text-subtle);
}

.Text--disabled {
  color: var(--text-disabled);
}

.Text--destructive {
  color: var(--text-destructive);
}

.Text--white {
  color: var(--text-white);
}

.Text--success {
  color: var(--text-success);
}

.Text--link {
  color: var(--text-link);
}

.Text--medium {
  font-weight: var(--font-weight-medium);
}

.Text--strong {
  font-weight: var(--font-weight-bold);
}

/* Heading */
.Heading {
  margin: 0;
}

.Heading--s {
  font-weight: var(--font-weight-bold);
  font-size: var(--font-size-m);
  line-height: var(--font-height-m);
}

.Heading--m {
  font-weight: var(--font-weight-medium);
  font-size: var(--font-size-l);
  line-height: var(--font-height-l);
}

.Heading--l {
  font-weight: var(--font-weight-normal);
  font-size: var(--font-size-xl);
  line-height: var(--font-height-xl);
}

.Heading--xl {
  font-weight: var(--font-weight-medium);
  font-size: var(--font-size-xxl);
  line-height: var(--font-height-xxl);
}

.Heading--xxl {
  font-weight: var(--font-weight-normal);
  font-size: var(--font-size-xxxl);
  line-height: var(--font-height-xxl);
}

.Heading--default {
  color: var(--text);
}

.Heading--subtle {
  color: var(--text-subtle);
}

.Heading--disabled {
  color: var(--text-disabled);
}

.Heading--white {
  color: var(--text-white);
}

/* Label */
.Label {
  display: inline-flex;
}

.Label--withInput {
  margin-bottom: var(--spacing-m);
}

.Label--optional {
  align-items: center;
}

.Label-text {
  line-height: var(--font-height-s);
  font-weight: var(--font-weight-medium);
}

.Label-optionalText {
  line-height: var(--font-height-s);
  margin-left: var(--spacing-s);
}

.Label--disabled {
  color: var(--text-disabled);
}

.Label-requiredIndicator {
  height: var(--spacing-m);
  width: var(--spacing-m);
  border-radius: 50%;
  background: var(--alert);
  margin-left: var(--spacing-m);
}

/* Caption */
.Caption {
  display: flex;
  align-items: center;
  box-sizing: border-box;
}

.Caption--withInput {
  margin-top: var(--spacing-m);
}

.Caption-icon {
  display: flex;
  align-content: center;
  margin-right: var(--spacing-m);
}

.Caption--hidden {
  display: none;
}

/* Subheading */
.Subheading {
  margin: 0;
  font-weight: var(--font-weight-bolder);
  font-size: var(--spacing-l);
  line-height: var(--font-height-m);
  letter-spacing: var(--letter-spacing);
  text-transform: uppercase;
}

.Subheading--default {
  color: var(--text);
}

.Subheading--subtle {
  color: var(--text-subtle);
}

.Subheading--disabled {
  color: var(--text-disabled);
}

.Subheading--white {
  color: var(--text-white);
}

.VerificationCodeInput-Input {
  display: inline-flex;
  width: 40px;
}

.VerificationCodeInput-Input .Input-input {
  text-align: center;
}

.VerificationCodeInput-Input .Input-input::-webkit-inner-spin-button,
.VerificationCodeInput-Input .Input-input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.VerticalNav {
  display: inline-flex;
  flex-direction: column;
}

.VerticalNav--expanded {
  width: var(--spacing-9);
}

.VerticalNav-section {
  text-transform: uppercase;
  padding-left: var(--spacing-2);
  padding-top: var(--spacing-l);
  padding-bottom: 6px;
  margin-top: var(--spacing);
}

.VerticalNav-section--border {
  border-top: var(--spacing-xs) solid var(--secondary-light);
}

.MenuItem {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.MenuItem--horizontal {
  height: var(--spacing-3);
  padding-right: var(--spacing-l);
  padding-left: var(--spacing-l);
  border-radius: var(--spacing-2);
  margin: 0 var(--spacing-xs);
}

.MenuItem--vertical {
  margin-top: var(--spacing-m);
  margin-bottom: var(--spacing-m);
}

.MenuItem--collapsed {
  height: 28px;
  width: 28px;
  margin-left: 6px;
  margin-right: 6px;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
}

.MenuItem--expanded {
  padding-left: var(--spacing-2);
  padding-top: var(--spacing-m);
  padding-bottom: var(--spacing-m);
  margin-right: var(--spacing);
  justify-content: space-between;
  border-radius: 0px var(--spacing-2) var(--spacing-2) 0px;
}

.MenuItem--disabled {
  pointer-events: none;
}

.MenuItem:hover {
  background-color: var(--secondary-light);
}

.MenuItem--active,
.MenuItem--active:hover {
  background: var(--primary-lightest);
}

.MenuItem--rounded {
  border-radius: var(--spacing-2);
}

.MenuItem--subMenu {
  padding-left: 40px;
}

.MenuItem .Text {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.MenuItem-count {
  margin-right: var(--spacing-m);
}

.MenuItem-count--disabled {
  opacity: 0.6;
}
